<script>
export default {
  data() {
    return {
      // Êé®ËçêÊ∏ÖÂçïÊ®°Êùø
      recommendLists: [
        {
          id: 1,
          name: '3Â§©Ë•øÂÆâÂêÉÈù¢Ë∑ØÁ∫ø',
          type: 'Èù¢È£üÂØªÂë≥',
          items: [
            'Day1: bi√°ngbi√°ngÈù¢ ‚Üí Á¢ëÊûóÂçöÁâ©È¶Ü',
            'Day2: Ê≤πÊ≥ºÈù¢ ‚Üí ÂõûÊ∞ëË°óÂ∞èÂêÉ',
            'Day3: Â≤êÂ±±ËáäÂ≠êÈù¢ ‚Üí ÂüéÂ¢ôÈ™ëË°å'
          ]
        },
        {
          id: 2,
          name: 'ÂÆùÈ∏°Èù¢È£ü‰∏ÄÊó•Ê∏∏',
          type: 'Èù¢È£üÂØªÂë≥',
          items: [
            '‰∏äÂçà: Â≤êÂ±±ËáäÂ≠êÈù¢ÔºàËÄÅÂ≠óÂè∑Ôºâ',
            '‰∏≠Âçà: ÊìÄÈù¢ÁöÆÈÖçÂáâËèú',
            '‰∏ãÂçà: Ê≥ïÈó®ÂØ∫ÂèÇËßÇ',
            'Êôö‰∏ä: Êâ∂È£éÈπøÁ≥ïÈ¶ç'
          ]
        },
        {
          id: 3,
          name: 'Ë•øÂÆâÈùûÈÅó‰∏ÄÊó•ÊâìÂç°',
          type: 'ÈùûÈÅó‰ΩìÈ™å',
          items: [
            '‰∏äÂçà10:00: ÁöÆÂΩ±ÊàèË°®ÊºîÔºàÊèêÂâçÈ¢ÑÁ∫¶Ôºâ',
            '‰∏ãÂçà2:00: Ââ™Á∫∏‰ΩìÈ™åËØæ',
            'Êôö‰∏ä7:00: Áß¶ËÖîÊºîÂá∫ÔºàÊòì‰øóÁ§æÔºâ'
          ]
        },
        {
          id: 4,
          name: 'Êò•Â≠£ÊßêËä±È∫¶È•≠',
          type: 'Â≠£ËäÇÈôêÂÆö',
          items: [
            'ÂØªÊâæÊßêËä±ÁõõÂºÄÁöÑÂÜúÂÆ∂',
            '‰ΩìÈ™åÊßêËä±È∫¶È•≠Âà∂‰Ωú',
            'ÂìÅÂ∞ùÂÜúÂÆ∂ÈáéËèú',
            'Âë®ËæπÂ±±ÊûóË∏èÊò•'
          ]
        },
        {
          id: 5,
          name: 'ÁßãÂ≠£ÊüøÂ≠ê‰πãÊóÖ',
          type: 'Â≠£ËäÇÈôêÂÆö',
          items: [
            'ÂØåÂπ≥ÊüøÂ≠êÂõ≠ÈááÊëò',
            'ÊüøÈ•ºÂà∂‰Ωú‰ΩìÈ™å',
            'ÂìÅÂ∞ùÊüøÂ≠êÁõ∏ÂÖ≥ÁæéÈ£ü',
            'Ë¥≠‰π∞ÊüøÈ•º‰º¥ÊâãÁ§º'
          ]
        },
        {
          id: 6,
          name: 'ÈùûÈÅóÊâãÂ∑•‰ΩìÈ™å',
          type: 'ÈùûÈÅó‰ΩìÈ™å',
          items: [
            'Âá§ÁøîÊ≥•Â°ëÂ∑•‰ΩúÂùä',
            'Â≠¶‰π†Âà∂‰ΩúÊ≥•Â°ë',
            'ÂèÇËßÇÈùûÈÅóÂ±ïËßàÈ¶Ü',
            'Ë¥≠‰π∞Ê≥•Â°ë‰ΩúÂìÅ'
          ]
        }
      ],
      // ÊàëÁöÑÊ∏ÖÂçïÂàóË°®
      myChecklists: [
        {
          id: 'my1',
          name: 'ÊàëÁöÑÊ∏∏Ë°åÊ∏ÖÂçï',
          date: '2025-11-27',
          category: 'food',
          items: [
            { id: 'item1', content: 'ÂéªËÄÅÂ≠ôÂÆ∂ÂêÉÁæäËÇâÊ≥°È¶ç', checked: false, note: 'Ë¶ÅÂä†Âèå‰ªΩËáäÂ≠ê' },
            { id: 'item2', content: 'Á¢ëÊûóÂçöÁâ©È¶ÜÂèÇËßÇ', checked: true, note: '' },
            { id: 'item3', content: '‰π∞Âá§ÁøîÊ≥•Â°ë‰Ωú‰º¥ÊâãÁ§º', checked: false, note: '' }
          ]
        }
      ],
      // Á±ªÂà´‰∏éÁ¥¢Âºï
      categories: [
        { key: 'food', label: 'ÊàëÁöÑÁæéÈ£üÊ∏ÖÂçï' },
        { key: 'heritage', label: 'ÊàëÁöÑÈùûÈÅóÊ∏ÖÂçï' },
        { key: 'season', label: 'ÊàëÁöÑÂ≠£ËäÇÊ∏ÖÂçï' }
      ],
      currentCategory: 'food',
      categoryIndexMap: { food: 0, heritage: 0, season: 0 },
      // Êó•ÊúüÂºπÁ™ó
      showDateDialog: false,
      dateTemp: '',
      selectedDate: new Date().toISOString().split('T')[0],
      // ÂΩìÂâçÊòæÁ§∫ÁöÑÊ∏ÖÂçïÁ¥¢ÂºïÔºàÂÖºÂÆπÊóßÂ≠óÊÆµÔºå‰∏çÂÜçÁõ¥Êé•‰ΩøÁî®Ôºâ
      currentListIndex: 0,
      // Â∫ïÈÉ®ÂõæÊ†á
      bottomIcons: [
        { id: 'qingdan', label: 'ÈôïÊ∏∏ËÆ∞', img: '/static/assets/retouch_2025112414391093.png' },
        { id: 'fengwei', label: 'ÈôïÂë≥ÈõÜ', img: '/static/assets/retouch_2025112414392946.png' },
        { id: 'feiyi', label: 'ÈôïÈùûÈÅó', img: '/static/assets/retouch_2025112414400271.png' },
        { id: 'profile', label: '‰∏™‰∫∫‰∏≠ÂøÉ', img: '/static/assets/retouch_2025112414394823.png' }
      ],
      // ÂºπÁ™óÊéßÂà∂
      showAddDialog: false,
      showTemplateDialog: false,
      showCreateListDialog: false,
      showRecommendDetail: false,
      selectedRecommend: null,
      newItemContent: '',
      newItemNote: '',
      newListName: '',
      // ÈáçÂëΩÂêçÂºπÁ™ó‰∏é‰∏ä‰∏ãÊñá
      showRenameDialog: false,
      renameTemp: '',
      targetRenameList: null,
      // Ê∑ªÂä†È°π/Êó•Êúü‰∏ä‰∏ãÊñá
      activeListForAdd: null,
      activeDateList: null
    }
  },
  computed: {
    filteredLists() {
      return this.myChecklists.filter(l => l.category === this.currentCategory)
    },
    // ÂΩìÂâçÈÄâ‰∏≠Êó•ÊúüÁöÑÊ∏ÖÂçï
    displayedLists() {
      return this.filteredLists.filter(l => l.date === this.selectedDate)
    },
    // ÊåâÊó•ÊúüÂàÜÁªÑÔºåÂΩìÂâçÂàÜÁ±ªÂÜÖ
    groupedListsByDate() {
      const groups = {}
      this.filteredLists.forEach(l => {
        const d = l.date || 'Êú™ËÆæÁΩÆÊó•Êúü'
        if (!groups[d]) groups[d] = []
        groups[d].push(l)
      })
      // Êó•ÊúüÂÄíÂ∫èÔºàÊú™ËÆæÁΩÆÊó•ÊúüÊîæÊúÄÂêéÔºâ
      const sortedDates = Object.keys(groups).sort((a, b) => {
        if (a === 'Êú™ËÆæÁΩÆÊó•Êúü') return 1
        if (b === 'Êú™ËÆæÁΩÆÊó•Êúü') return -1
        return a > b ? -1 : (a < b ? 1 : 0)
      })
      return sortedDates.map(d => ({
        date: d,
        lists: groups[d]
      }))
    },
    currentList() {
      const idx = this.categoryIndexMap[this.currentCategory] || 0
      return this.filteredLists[idx] || null
    },
    categoryLabel() {
      const map = { food: 'ÊàëÁöÑÁæéÈ£üÊ∏ÖÂçï', heritage: 'ÊàëÁöÑÈùûÈÅóÊ∏ÖÂçï', season: 'ÊàëÁöÑÂ≠£ËäÇÊ∏ÖÂçï' }
      return map[this.currentCategory] || ''
    }
  },
  methods: {
    // Êü•ÁúãÊé®ËçêÊ∏ÖÂçïËØ¶ÊÉÖ
    viewRecommendDetail(list) {
      this.selectedRecommend = list
      this.showRecommendDetail = true
    },
    
    // ÂÖ≥Èó≠Êé®ËçêÊ∏ÖÂçïËØ¶ÊÉÖ
    closeRecommendDetail() {
      this.showRecommendDetail = false
      this.selectedRecommend = null
    },
    
    // ‰ªéËØ¶ÊÉÖÈ°µÂØºÂÖ•Ê∏ÖÂçï
    importFromDetail() {
      if (!this.selectedRecommend) return
      
      const newList = {
        id: 'my' + Date.now(),
        name: this.selectedRecommend.name,
        date: this.selectedDate,
        category: this.currentCategory,
        items: this.selectedRecommend.items.map((item, index) => ({
          id: 'item' + Date.now() + index,
          content: item,
          checked: false,
          note: ''
        }))
      }
      this.myChecklists.push(newList)
      this.categoryIndexMap[this.currentCategory] = this.filteredLists.length // Êñ∞Â¢ûÂú®Êú´Â∞æ
      this.closeRecommendDetail()
      uni.showToast({ title: 'ÂØºÂÖ•ÊàêÂäü', icon: 'success' })
    },
    
    // ÂàáÊç¢Ê∏ÖÂçïÔºàÂΩìÂâçÁ±ªÂà´ÂÜÖÂæ™ÁéØÔºâ
    switchList() {
      const lists = this.filteredLists
      if (lists.length === 0) {
        uni.showToast({ title: 'ËØ•Á±ªÂà´ÊöÇÊó†Ê∏ÖÂçï', icon: 'none' })
        return
      }
      const cur = this.categoryIndexMap[this.currentCategory] || 0
      this.categoryIndexMap[this.currentCategory] = (cur + 1) % lists.length
    },
    
    // ÂàáÊç¢ÂÆåÊàêÁä∂ÊÄÅ
    toggleCheck(item, list) {
      item.checked = !item.checked
      if (item.checked) {
        uni.showToast({ title: 'Â∑≤ÂÆåÊàê', icon: 'success' })
      }
    },
    
    // Âà†Èô§Ê∏ÖÂçïÈ°π
    deleteItem(item, list) {
      if (!list) return
      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'Á°ÆÂÆöÂà†Èô§Ëøô‰∏™Ê∏ÖÂçïÈ°πÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            const index = list.items.findIndex(i => i.id === item.id)
            if (index > -1) {
              list.items.splice(index, 1)
              uni.showToast({ title: 'Â∑≤Âà†Èô§', icon: 'success' })
            }
          }
        }
      })
    },
    
    // ÊòæÁ§∫Ê∑ªÂä†Ê∏ÖÂçïÈ°πÂºπÁ™ó
    showAddItem(list) {
      this.activeListForAdd = list || this.currentList || null
      if (!this.activeListForAdd) {
        uni.showToast({ title: 'ËØ∑ÂÖàÂàõÂª∫Ê∏ÖÂçï', icon: 'none' })
        return
      }
      this.newItemContent = ''
      this.newItemNote = ''
      this.showAddDialog = true
    },
    
    // Ê∑ªÂä†Ê∏ÖÂçïÈ°π
    addItem() {
      if (!this.newItemContent.trim()) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÂÜÖÂÆπ', icon: 'none' })
        return
      }
      const target = this.activeListForAdd
      if (!target) {
        uni.showToast({ title: 'ËØ∑ÂÖàÈÄâÊã©Ê∏ÖÂçï', icon: 'none' })
        return
      }
      const newItem = {
        id: 'item' + Date.now(),
        content: this.newItemContent.trim(),
        checked: false,
        note: this.newItemNote.trim()
      }
      target.items.push(newItem)
      this.showAddDialog = false
      this.activeListForAdd = null
      uni.showToast({ title: 'Ê∑ªÂä†ÊàêÂäü', icon: 'success' })
    },
    
    // ÂØºÂá∫Ê∏ÖÂçï
    exportList() {
      if (!this.currentList) {
        uni.showToast({ title: 'ÊöÇÊó†Ê∏ÖÂçï', icon: 'none' })
        return
      }
      
      let text = `„Äê${this.currentList.name}„Äë\nÊó•ÊúüÔºö${this.currentList.date}\n\n`
      this.currentList.items.forEach((item, index) => {
        const status = item.checked ? '‚òë' : '‚òê'
        text += `${index + 1}. ${status} ${item.content}\n`
        if (item.note) {
          text += `   Â§áÊ≥®Ôºö${item.note}\n`
        }
      })
      
      // Â∞ÜÊñáÊú¨Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø
      uni.setClipboardData({
        data: text,
        success: () => {
          uni.showToast({ title: 'Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø', icon: 'success' })
        }
      })
    },
    
    // ÊòæÁ§∫Ê∏ÖÂçïÊ®°ÊùøÈÄâÊã©
    showTemplates() {
      this.showTemplateDialog = true
    },

    // ÂàáÊç¢Á±ªÂà´
    switchCategory(key) {
      if (this.currentCategory === key) return
      this.currentCategory = key
      if (!this.filteredLists.length) {
        this.categoryIndexMap[key] = 0
      } else {
        this.categoryIndexMap[key] = this.categoryIndexMap[key] || 0
      }
    },

    // ÊâìÂºÄÊó•ÊúüÈÄâÊã©ÔºàÂÖ®Â±ÄÔºâ
    openDateDialog() {
      this.dateTemp = this.selectedDate
      this.showDateDialog = true
    },

    // Êó•ÊúüÂèòÊõ¥
    onDateChange(e) {
      this.dateTemp = e.detail.value
    },

    // Á°ÆËÆ§Êó•ÊúüÔºàÂÖ®Â±ÄÔºâ
    confirmDate() {
      if (this.dateTemp) {
        this.selectedDate = this.dateTemp
        uni.showToast({ title: 'Â∑≤ÂàáÊç¢Êó•Êúü', icon: 'success' })
      }
      this.showDateDialog = false
    },
    
    // ÈáçÂëΩÂêçÊ∏ÖÂçï
    renameList(list) {
      this.targetRenameList = list
      this.renameTemp = (list && list.name) || ''
      this.showRenameDialog = true
    },
    
    confirmRename() {
      if (!this.targetRenameList) {
        this.showRenameDialog = false
        return
      }
      const name = (this.renameTemp || '').trim()
      if (!name) {
        uni.showToast({ title: 'ÂêçÁß∞‰∏çËÉΩ‰∏∫Á©∫', icon: 'none' })
        return
      }
      this.targetRenameList.name = name
      this.showRenameDialog = false
      this.targetRenameList = null
      uni.showToast({ title: 'Â∑≤ÈáçÂëΩÂêç', icon: 'success' })
    },

    // ‰ªéÊú¨Âú∞Êñá‰ª∂ÂØºÂÖ•
    importFromLocal() {
      const self = this
      if (typeof window !== 'undefined' && uni.chooseFile) {
        uni.chooseFile({
          count: 1,
          type: 'all',
          success(res) {
            const file = res.tempFiles && res.tempFiles[0]
            if (!file) {
              uni.showToast({ title: 'Êú™ÈÄâÊã©Êñá‰ª∂', icon: 'none' })
              return
            }
            if (typeof FileReader !== 'undefined') {
              const reader = new FileReader()
              reader.onload = function(evt) {
                const text = evt.target.result
                self._handleImportedContent(text)
              }
              reader.onerror = function() {
                uni.showToast({ title: 'ËØªÂèñÂ§±Ë¥•', icon: 'none' })
              }
              reader.readAsText(file, 'utf-8')
            } else {
              uni.showToast({ title: 'ÂΩìÂâçÁéØÂ¢É‰∏çÊîØÊåÅËØªÂèñÊñá‰ª∂', icon: 'none' })
            }
          },
          fail() {
            uni.showToast({ title: 'ÈÄâÊã©Â§±Ë¥•', icon: 'none' })
          }
        })
      } else {
        uni.showToast({ title: 'ÂΩìÂâçÁ´ØÊöÇ‰∏çÊîØÊåÅÊú¨Âú∞ÂØºÂÖ•', icon: 'none' })
      }
    },

    // Â§ÑÁêÜÂØºÂÖ•ÊñáÊú¨/JSON
    _handleImportedContent(text) {
      try {
        const data = JSON.parse(text)
        const lists = Array.isArray(data) ? data : (data.lists || [data])
        lists.forEach(raw => {
          const name = raw.name || 'ÂØºÂÖ•Ê∏ÖÂçï'
          const date = raw.date || this.selectedDate
          const itemsArr = raw.items || []
          const newList = {
            id: 'my' + Date.now() + Math.random().toString(16).slice(2,6),
            name,
            date,
            category: this.currentCategory,
            items: itemsArr.map((it, idx) => {
              if (typeof it === 'string') {
                return { id: 'item' + Date.now() + idx, content: it, checked: false, note: '' }
              }
              return {
                id: 'item' + Date.now() + idx,
                content: it.content || it.text || '',
                checked: !!it.checked,
                note: it.note || ''
              }
            })
          }
          this.myChecklists.push(newList)
          this.categoryIndexMap[this.currentCategory] = this.filteredLists.length - 1
        })
        uni.showToast({ title: 'ÂØºÂÖ•ÊàêÂäü', icon: 'success' })
      } catch (e) {
        const lines = (text || '').split(/\r?\n/).map(s => s.trim()).filter(Boolean)
        if (!lines.length) {
          uni.showToast({ title: 'Êñá‰ª∂‰∏∫Á©∫', icon: 'none' })
          return
        }
        const name = lines[0]
        const items = lines.slice(1)
        const newList = {
          id: 'my' + Date.now(),
          name: name || 'ÂØºÂÖ•Ê∏ÖÂçï',
          date: this.selectedDate,
          category: this.currentCategory,
          items: items.map((s, idx) => ({ id: 'item' + Date.now() + idx, content: s, checked: false, note: '' }))
        }
        this.myChecklists.push(newList)
        this.categoryIndexMap[this.currentCategory] = this.filteredLists.length - 1
        uni.showToast({ title: 'ÂØºÂÖ•ÊàêÂäü', icon: 'success' })
      }
    },
    
    // ÂØºÂÖ•Ê®°Êùø
    importTemplate(template) {
      const newList = {
        id: 'my' + Date.now(),
        name: template.name,
        date: this.selectedDate,
        category: this.currentCategory,
        items: template.items.map((item, index) => ({
          id: 'item' + Date.now() + index,
          content: item,
          checked: false,
          note: ''
        }))
      }
      this.myChecklists.push(newList)
      this.categoryIndexMap[this.currentCategory] = this.filteredLists.length // ÊåáÂêëÊñ∞ÂØºÂÖ•
      this.showTemplateDialog = false
      uni.showToast({ title: 'ÂØºÂÖ•ÊàêÂäü', icon: 'success' })
    },
    
    // ÊòæÁ§∫ÂàõÂª∫Ê∏ÖÂçïÂºπÁ™ó
    showCreateList() {
      this.newListName = ''
      this.showCreateListDialog = true
    },
    
    // ÂàõÂª∫Êñ∞Ê∏ÖÂçï
    createNewList() {
      if (!this.newListName.trim()) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•Ê∏ÖÂçïÂêçÁß∞', icon: 'none' })
        return
      }
      
      const newList = {
        id: 'my' + Date.now(),
        name: this.newListName.trim(),
        date: this.selectedDate,
        category: this.currentCategory,
        items: []
      }
      
      this.myChecklists.push(newList)
      this.categoryIndexMap[this.currentCategory] = this.filteredLists.length
      this.showCreateListDialog = false
      uni.showToast({ title: 'ÂàõÂª∫ÊàêÂäü', icon: 'success' })
    },
    
    // È°µÈù¢Ë∑≥ËΩ¨
    goPage(id) {
      const routes = {
        tong: '/pages/index/index',
        fengwei: '/pages/flavor/index',
        qingdan: '/pages/checklist/index',
        profile: '/pages/mine/index',
        feiyi: '/pages/heritage/index'
      }
      const url = routes[id]
      if (!url) {
        uni.showToast({ title: 'È°µÈù¢ÂºÄÂèë‰∏≠', icon: 'none' })
        return
      }
      
      // Â¶ÇÊûúÊòØÂΩìÂâçÈ°µÔºå‰∏çË∑≥ËΩ¨
      const pages = getCurrentPages()
      const currentPage = pages[pages.length - 1]
      if (currentPage && ('/' + currentPage.route) === url) {
        return
      }
      
      if (url === '/pages/index/index') {
        uni.reLaunch({ url })
      } else {
        uni.navigateTo({ url })
      }
    }
  }
}
</script>

<template>
  <page-meta page-style="overflow:auto;min-height:100vh" />
  <view class="checklist-page">
    
    <!-- Â∑¶‰æßÊé®ËçêÊ∏ÖÂçï -->
    <view class="left-section">
      <view class="recommend-title">Êé®ËçêÊ∏ÖÂçï</view>
      <scroll-view class="recommend-scroll" scroll-y>
        <view 
          v-for="list in recommendLists" 
          :key="list.id" 
          class="recommend-card"
          @tap="viewRecommendDetail(list)"
        >
          <view class="recommend-type">{{ list.type }}</view>
          <view class="recommend-name">{{ list.name }}</view>
        </view>
      </scroll-view>
      
      <!-- Â∑¶‰∏ãËßíÂäüËÉΩÊåâÈíÆ -->
      <view class="left-bottom-actions">
        <view class="left-action-btn" @tap="exportList">
          <text class="left-action-text">ÂØºÂá∫Ê∏ÖÂçï</text>
        </view>
        <view class="left-action-btn" @tap="importFromLocal">
          <text class="left-action-text">‰∏ÄÈîÆÂØºÂÖ•</text>
        </view>
      </view>
    </view>
    
